var wx = require('weixin-js-sdk');
export default {
  name: "Share",
  props:["share"],
  mounted() {
     wx.config({
      debug: true, //
      appId: '', //
      timestamp: '',//
      nonceStr: '',//
      signature: '',//
      jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] //js
    });
  }, 
  methods: {
    hideModal(){
      this.$emit("changeShare", 0);
    },
    //
    onMenuShareAppMessage: function(){
    wx.onMenuShareAppMessage({
      title: '', // 
      desc: '', // 
      link: '', // 
      imgUrl: '', // 
      type: '', // ,musicvideolinklink
      dataUrl: '', // typemusicvideo
      success: function () {
          // 
          alert("");
      },
      cancel: function () {
          // 
          alert("");
      }
    });
    },
  //
  onMenuShareTimeline: function() {
  wx.onMenuShareTimeline({
    title: '', // 
    link: '', // 
    imgUrl: '', // 
    success: function () {
        // 
    },
    cancel: function () {
        // 
    }
    });
  }
},
  data() {
    return {
      
    }
  },
}



